/* noto-sans-kr-300 - korean */
@font-face {
  font-family: 'Noto Sans KR';
  font-style: normal;
  font-weight: 300;
  src: url('./noto-sans-kr-v8-korean-300.eot'); /* IE9 Compat Modes */
  src: local('Noto Sans KR Light'), local('NotoSansKR-Light'),
  url('./noto-sans-kr-v8-korean-300.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
  url('./noto-sans-kr-v8-korean-300.woff2') format('woff2'), /* Super Modern Browsers */
  url('./noto-sans-kr-v8-korean-300.woff') format('woff'), /* Modern Browsers */
  url('./noto-sans-kr-v8-korean-300.svg#NotoSansKR') format('svg'); /* Legacy iOS */
}
