html * { 
  // 고정뷰포트 크롬에러 방지 코드 // max-height:10000000px;
  box-sizing: border-box;
}
html, body, ul, ol, p, dl, dt, dd, button, table, th, td, textarea, select, mark, s, 
figure, figcaption, video, audio, option, input, button, strong, b, em, i, li, img {
  margin: 0;
  padding: 0;
}
h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
  font-size: 1em;
  font-weight: normal;
}
li {
  list-style-type: none;
}
a {
  background-color: transparent; /* IE 10 */
  color: inherit;
  text-decoration: none;
}
strong, th, b {
  font-weight: normal;
}
em, i, address {
  font-style: normal;
}
mark {
  background-color: transparent;
}
table {
  table-layout: fixed;
  word-break: break-all;
  border-collapse: collapse;
  border-spacing: 0;
  font-size: inherit;
}
img {
  border-style: none; /* IE 10 */
}
[hidden] {
  display: none; /* IE 10 */
}
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section, main { display: block; }

/* form */
fieldset{
  position: relative;
  border-width: 0;
}
legend {
  position: absolute;
  top: 0;
  left: 0;
}
input {
  box-sizing: border-box;
  background-color: transparent;
  border: 0;
  border-radius: 0;
  line-height: 1;
  font: inherit;
  outline: 0;
  -webkit-appearance: none;
}
input[type=search] {
	-webkit-appearance: none;
	border: 0;
	border-radius: 0;
}
button {
  box-sizing: border-box;
  border: none;
  background: transparent;
  font: inherit;
  outline: 0;
  line-height: 1;
  letter-spacing: 0;
  /* Corrects inability to style clickable `input` types in iOS */
  -webkit-appearance: none;
  cursor: pointer;
}
textarea {
  box-sizing: border-box;
  -webkit-appearance: none;
  font: inherit;
  border: 0;
  resize: none;
  outline: 0;
  display: block;
  overflow: auto;
}
select {
  box-sizing: border-box;
  border: 0;
  font: inherit;
  vertical-align: middle;
  cursor: pointer;
  outline: 0;

  -webkit-appearance: none;
     -moz-appearance: none;
      -ms-appearance: none;
       -o-appearance: none;
          appearance: none;
}
select::-ms-expand, /* select 화살표 */
input::-ms-reveal, /* password eye */
input::-ms-clear /* input x 버튼 */ {
  display: none;
}
/* placeholder */
::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #999;
  opacity: 1; /* Firefox */
}
:-ms-input-placeholder { /* Internet Explorer 10-11 */
  color: #999;
}  
::-ms-input-placeholder { /* Microsoft Edge */
  color: #999;
}