/* Util */
/* Lauout */
.l-wrapper {
  box-sizing: border-box;
  width: 1480px;
  min-width: 1180px;
  max-width: 100%;
  margin: 0 auto; }

.l-row {
  display: flex;
  flex-wrap: wrap;
  margin: -20px; }

.l-col {
  padding: 20px; }

.l-col-1-100 {
  width: 100%; }

.l-col-2-100 {
  width: 50%; }

.l-col-3-100 {
  width: 33.33333333333334%; }

.l-col-4-100 {
  width: 25%; }

.l-col-5-100 {
  width: 20%; }

/* util */
.u-clearfix::after {
  content: '';
  display: block;
  clear: both; }

.u-hidden {
  overflow: hidden;
  position: absolute;
  left: -10000px;
  clip: rect(1, 1, 1, 1);
  width: 1px;
  height: 1px;
  border: 0; }

caption.u-hidden {
  position: static; }

.u-ls-number {
  letter-spacing: 0; }

.u-ls-korean {
  letter-spacing: -0.5px; }

.u-emphasize {
  color: #04a9ff; }

.u-tar {
  text-align: right; }

.u-tac {
  text-align: center; }

.u-tal {
  text-align: left; }

.u-por {
  position: relative; }

.u-gray {
  color: #999999; }

.u-w100 {
  width: 100%; }

/* Basic */
html {
  font-size: 62.5%; }

body {
  font-family: 'Roboto', 'Noto Sans KR', 맑은고딕, "Malgun Gothic", 돋움, sans-serif;
  font-weight: 400;
  line-height: 1;
  font-size: 1.6rem;
  background: url(./assets/images/common/bg_visual.png), linear-gradient(90deg, #461db0 40%, #1347cd 100%);
  background-repeat: no-repeat;
  background-position: center top;
  min-height: 100vh; }

.page_upper {
  padding: 60px 0; }

.table_type01 {
  width: 100%; }
  .table_type01 .link {
    text-decoration: underline; }
  .table_type01 th {
    background-color: #f1f1f1;
    height: 60px;
    font-weight: 500;
    font-size: 14px;
    word-break: keep-all; }
  .table_type01 td {
    border-bottom: 1px solid #e2e2e2;
    height: 50px;
    text-align: center;
    font-size: 14px;
    color: #333333; }

.table_type02 {
  width: 100%;
  font-size: 14px; }
  .table_type02 tr:first-child th, .table_type02 tr:first-child td {
    border-top: 1px solid #e2e2e2; }
  .table_type02 th {
    background-color: #f1f1f1;
    text-align: left;
    padding: 0 40px;
    font-weight: 500;
    width: 20%;
    border-bottom: 1px solid #e2e2e2;
    word-break: keep-all; }
  .table_type02 td {
    text-align: left;
    padding: 0 10px 0 40px;
    color: #333333;
    border-bottom: 1px solid #e2e2e2;
    height: 60px; }
  .table_type02 .link {
    text-decoration: underline; }
